<h1 id="什么是微服务" tabindex="-1"><a class="header-anchor" href="#什么是微服务" aria-hidden="true">#</a> 什么是微服务</h1>
<p>很小很小的服务，一个服务对应一个单一的功能，单独部署</p>
<h1 id="微服务与分布式的区别" tabindex="-1"><a class="header-anchor" href="#微服务与分布式的区别" aria-hidden="true">#</a> 微服务与分布式的区别</h1>
<p>微服务是架构设计，分布式是部署方式，概念不同</p>
<p>服务之间的逻辑不要偶尔，比如商品库服务的逻辑不要写在物流服务里面</p>
<p>微服务设计原则</p>
<ul>
<li>单一职责原则，每个服务只要实现自己的业务</li>
<li>服务自治</li>
</ul>
<p>微服务设计原则，解决方案</p>
<p>采用微服务架构来构建系统，会带来清晰的业务划分和可扩展性。</p>
<p>服务化的核心就是将传统的一站式应用拆分成多个服务，这些服务不再公用<code>DB</code>等</p>
